.siteFooter {
  padding: var(--space-5) 0 var(--space-3);
  border-top: 1px solid var(--accent);
  background: #f1ede9;
}

.footerGrid {
  display: grid;
  gap: var(--space-4);
}

.footerBrand {
  display: grid;
  gap: 0.75rem;
}

.footerLogoRow {
  display: inline-flex;
  align-items: center;
}

.footerLogo {
  height: 40px;
  width: auto;
  display: block;
}

.footerWordmark {
  font-weight: 700;
  letter-spacing: 0.14em;
  font-size: 0.9rem;
}

.footerSocials {
  display: flex;
  gap: 0.6rem;
}

.footerSocials a {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1px solid var(--accent);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--muted);
}

.footerSocials svg {
  width: 16px;
  height: 16px;
  display: block;
}

.footerCol h3 {
  margin-bottom: var(--space-1);
}

.footerCol a,
.footerCol p {
  color: var(--muted);
  display: block;
  margin-bottom: 0.5rem;
}

.footerDivider {
  height: 1px;
  background: var(--accent);
  margin-top: var(--space-4);
}

.footerBottom {
  text-align: center;
  color: var(--muted);
  margin-top: var(--space-3);
}

@media (min-width: 768px) {
  .footerGrid {
    grid-template-columns: minmax(180px, 1.2fr) repeat(4, minmax(140px, 1fr));
  }
}
