:root {
  --bg: #f4f3f1;
  --text: #111111;
  --muted: #5a5854;
  --accent: #cfc8bf;
  --card: #ffffff;
  --shadow: 0 20px 40px rgba(17, 17, 17, 0.08);
  --radius-lg: 28px;
  --radius-md: 16px;
  --radius-sm: 10px;
  --space-1: 0.5rem;
  --space-2: 1rem;
  --space-3: 1.5rem;
  --space-4: 2rem;
  --space-5: 3rem;
  --space-6: 4rem;
  --max-width: 1200px;
  --font: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  --motion-fast: 160ms;
  --motion-medium: 320ms;
  --motion-slow: 520ms;
  --ease: cubic-bezier(0.2, 0.8, 0.2, 1);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font);
  color: var(--text);
  background: var(--bg);
  line-height: 1.6;
}

a {
  color: inherit;
  text-decoration: none;
}

button,
input {
  font: inherit;
}

:focus-visible {
  outline: 2px solid var(--text);
  outline-offset: 3px;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 1rem;
  background: var(--text);
  color: var(--bg);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-sm);
  z-index: 999;
}

.skip-link:focus {
  top: 1rem;
}

.container {
  width: min(100% - 2rem, var(--max-width));
  margin: 0 auto;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.85rem 1.6rem;
  border-radius: 999px;
  border: 1px solid transparent;
  font-weight: 600;
  transition: transform var(--motion-fast) var(--ease),
    box-shadow var(--motion-fast) var(--ease),
    background-color var(--motion-fast) var(--ease);
}

.btn:hover {
  transform: translateY(-1px);
}

.btn:active {
  transform: translateY(1px);
}

.btn-primary {
  background: var(--text);
  color: var(--bg);
  box-shadow: 0 10px 20px rgba(17, 17, 17, 0.15);
}

.btn-primary:hover {
  background: #000000;
}

.btn-secondary {
  border-color: var(--text);
  color: var(--text);
  background: transparent;
}

.section-label {
  font-size: 0.85rem;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: var(--muted);
  margin-bottom: var(--space-2);
}

.section-header h2 {
  font-size: clamp(1.9rem, 4vw, 3rem);
  margin-bottom: var(--space-3);
}

/* Motion utilities */
.js-enabled [data-reveal],
.js-enabled .reveal-item {
  opacity: 0;
  transform: translateY(16px);
  transition: opacity var(--motion-medium) var(--ease),
    transform var(--motion-medium) var(--ease);
  will-change: opacity, transform;
}

.js-enabled [data-reveal].is-visible,
.js-enabled .reveal-item.is-visible {
  opacity: 1;
  transform: translateY(0);
}

@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }

  .btn {
    transition: none;
  }

  .js-enabled [data-reveal],
  .js-enabled .reveal-item {
    transition: none;
  }
}
