.siteHeader {
  position: sticky;
  top: 0;
  background: transparent;
  border-bottom: 1px solid transparent;
  backdrop-filter: none;
  z-index: 100;
  transition: box-shadow var(--motion-medium) var(--ease),
    border-color var(--motion-medium) var(--ease),
    background-color var(--motion-medium) var(--ease);
}

.siteHeader[data-scrolled="true"] {
    background: rgba(244, 243, 241, 0.92);
    border-bottom-color: rgba(17, 17, 17, 0.12);
    box-shadow: 0 10px 24px rgba(17, 17, 17, 0.08);
    backdrop-filter: blur(12px);
}

.headerInner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--space-2);
    padding: 0.15rem 0;
}

.logoWrap {
  display: inline-flex;
  align-items: center;
}

.logoImage {
    height: 42px;
    width: auto;
    display: block;
}

.mainNav {
  display: none;
  flex-direction: column;
  gap: var(--space-2);
  align-items: flex-start;
  padding: var(--space-3) 0;
}

.mainNavOpen {
  display: flex;
}

.navToggle {
  border: 1px solid var(--accent);
  background: transparent;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.navToggleBar {
  width: 18px;
  height: 2px;
  background: var(--text);
  border-radius: 999px;
}

.navCta {
    display: none;
    font-size: 0.95rem;
    font-weight: 500;
    padding: 0.4rem 0.85rem;
    line-height: 1;
}

@media (min-width: 768px) {
  .headerInner {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
  }

  .navToggle {
    display: none;
  }

  .mainNav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: var(--space-3);
  }

  .navCta {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
}
